
.page-datePick .head {
  position: fixed;
  top: 0;
  width: 100%;
}
.page-datePick .picker-toolbar {
  display: none;
}
.page-datePick .v-modal {
  display: none;
}
.picker-item .picker-item {
}
.picker-item.picker-selected {
  opacity: 1;
}
.page-datePick .mint-popup {

  position: fixed;
  top: 1rem;
  height: 3rem;
  overflow: hidden;
}
.page-datePick .picker-items {
  background: #999999;
  margin: 0 .2rem;
}
.page-datePick .tit {
  font-size: .17rem;
  margin: .15rem 0 .2rem .2rem;
  padding: 0;
  color: #222;
  text-align: left;
}



