.mulSelect .mask {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 22;
    top: 0;
    border-radius: .04rem;
}
.mulSelect .mask .shadow {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .14;
}
.mulSelect .mask .checked {
    position: absolute;
    right: 0.15rem;
    bottom: .1rem;
    width: .2rem;
    z-index: 99;
    opacity: 1;
}
.mulSelect .mask .checked img {
    width: 100%;
    border: none;
}



/*mask*/


/*double*/
.sing.double li {
    float: left;
    width: 50%;
}
.mulSelect ul {
    margin: 0 .1rem;
}
.mulSelect ul li .cont {
    margin: 0 .1rem;
}




/*double*/


/*progress*/
.progress {
    margin: .26rem .2rem 0 .2rem;
    overflow: hidden;
    text-align: right;
}
.mulSelect .memo {
    margin: .06rem 0 .15rem 0;
    color: #666;
}
.mulSelect .bottom {
    position: fixed;
    bottom: .08rem;
    width: 100%;
}




