/*common*/


/*mask*/
.mulSelect .mask[data-v-6d1bab64] {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 22;
    top: 0;
    border-radius: .04rem;
}
.mulSelect .mask .shadow[data-v-6d1bab64] {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .14;
}
.mulSelect .mask .checked[data-v-6d1bab64] {
    position: absolute;
    right: 0.15rem;
    bottom: .1rem;
    width: .2rem;
    z-index: 99;
    opacity: 1;
}
.mulSelect .mask .checked img[data-v-6d1bab64] {
    width: 100%;
    border: none;
}



/*mask*/


